const landfillColorMapping = (status: string): string => {
  switch (status) {
  case 'closed': return '#dd6b66';
  case 'open': return '#73a373';
  case 'construct': return '#eedd78';
  default: throw new Error('No such status');
  }
};

const statusMapping = {
  'open': '运行中',
  'closed': '已关闭',
  'construct': '建设中'
};
export {
landfillColorMapping,
statusMapping
};


