import React, { Component } from 'react';
import classes from './page5.less';
import landfills from '../../data/landfill';
import EChart from '../echarts/echarts';
import echarts from 'echarts';
import shenzhenJSON from '../../data/shenzhen';
import { scaleLog } from 'd3-scale';
import Carousel from 'nuka-carousel';
import LandfillCard from './landfillCard';
import { landfillColorMapping, statusMapping } from '../../utils/constants';
import _ from 'lodash';
echarts.registerMap('Shenzhen', shenzhenJSON);


/**
 * Created by aji on 16/3/23.
 */



const landfillDefaultAbility: number = 500;


const landfillSizeScaleLog = scaleLog()
    .domain([1, 1000])
    .range([5, 15]);

function landfillSizeScale(ability: number): number {
  if (ability < 1) {
    ability = landfillDefaultAbility;
  }
  return landfillSizeScaleLog(ability);
}

const dataConvert = l => {
  return {
    name: l.name,
    value: l.pos.split('|').map(p => +p).concat(l.capacity)
  };
};

const closed = landfills.filter(l => l.status === 'closed').map(dataConvert);
const open = landfills.filter(l => l.status === 'open').map(dataConvert);
const construct = landfills.filter(l => l.status === 'construct').map(dataConvert);


const defaultIndex = 0;
const option = () => {
  return {
    title : {
      text: '深圳市内垃圾场分布',
      left: 'center',
      padding: 20
    },
    legend: {
      orient: 'vertical',
      left: 'center',
      bottom: 'bottom',
      data:['已关闭垃圾场', '运行中垃圾场', '建设中垃圾场'],
      textStyle: {
        color: '#fff'
      }
    },
    geo: {
      map: 'Shenzhen',
      label: {
        emphasis: {
          show: false
        }
      },
      roam: false,
      itemStyle: {
        normal: {
          areaColor: '#323c48',
          borderColor: 'grey'
        },
        emphasis: {
          areaColor: '#2a333d'
        }
      }
    },
    series: [
      {
        name: '已关闭垃圾场',
        type: 'scatter',
        coordinateSystem: 'geo',
        data: closed,
        symbolSize: (val) => landfillSizeScale(val[2]),
        label: {
          normal: {
            formatter: '{b}',
            position: 'right',
            show: false
          },
          emphasis: {
            show: true
          }
        },
        itemStyle: {
          normal: {
            color: landfillColorMapping('closed')
          }
        }
      },
      {
        name: '运行中垃圾场',
        type: 'scatter',
        coordinateSystem: 'geo',
        data: open,
        symbolSize: (val) => landfillSizeScale(val[2]),
        label: {
          normal: {
            formatter: '{b}',
            position: 'right',
            show: false
          },
          emphasis: {
            show: true
          }
        },
        itemStyle: {
          normal: {
            color: landfillColorMapping('open')
          }
        }
      },
      {
        name: '建设中垃圾场',
        type: 'scatter',
        coordinateSystem: 'geo',
        data: construct,
        symbolSize: (val) => landfillSizeScale(val[2]),
        label: {
          normal: {
            formatter: '{b}',
            position: 'right',
            show: false
          },
          emphasis: {
            show: true
          }
        },
        itemStyle: {
          normal: {
            color: landfillColorMapping('construct')
          }
        }
      },
      {
        name: 'focusLandfill',
        type: 'effectScatter',
        coordinateSystem: 'geo',
        data: [dataConvert(landfills[defaultIndex])],
        symbolSize: (val) => landfillSizeScale(val[2]),
        showEffectOn: 'render',
        rippleEffect: {
          brushType: 'stroke'
        },
        hoverAnimation: true,
        label: {
          normal: {
            formatter: '{b}',
            position: 'right',
            show: true
          }
        },
        itemStyle: {
          normal: {
            color: landfillColorMapping(landfills[defaultIndex].status),
            shadowBlur: 10,
            shadowColor: '#333'
          }
        },
        zlevel: 1
      }
    ]
  }
};

const mapStyle = {
  height: '60%'
};

const cardStyle = {
  height: '40%'
};
const Decorators = [
  {
    component: React.createClass({
      render() {
        return (
            <button
                style={this.getButtonStyles(this.props.currentSlide === 0)}
                onClick={this.props.previousSlide}>PREV</button>
        )
      },
      getButtonStyles(disabled) {
        return {
          border: 0,
          background: 'rgba(0,0,0,0.4)',
          color: 'white',
          padding: 10,
          outline: 0,
          opacity: disabled ? 0.3 : 1,
          cursor: 'pointer'
        }
      }
    }),
    position: 'CenterLeft'
  },
  {
    component: React.createClass({
      render() {
        return (
            <button
                style={this.getButtonStyles(this.props.currentSlide + this.props.slidesToScroll >= this.props.slideCount)}
                onClick={this.props.nextSlide}>NEXT</button>
        )
      },
      getButtonStyles(disabled) {
        return {
          border: 0,
          background: 'rgba(0,0,0,0.4)',
          color: 'white',
          padding: 10,
          outline: 0,
          opacity: disabled ? 0.3 : 1,
          cursor: 'pointer'
        }
      }
    }),
    position: 'CenterRight'
  }
];

class Page extends Component {
  constructor(props) {
    super(props);
    this.state = {
      focusLandfill: defaultIndex,
      option: option()
    };
    this.afterSlide = this.afterSlide.bind(this);
    this.onClickLandfill = this.onClickLandfill.bind(this);
    this.init = this.init.bind(this);
  }
  afterSlide(index) {
    let opt = this.state.option;
    _.last(opt.series).data = [dataConvert(landfills[index])];
    _.last(opt.series).itemStyle.normal.color = landfillColorMapping(landfills[index].status);
    this.setState({
      focusLandfill: index,
      option: opt
    });
  }
  render() {
    const sliderSettings = {
      decorators: Decorators,
      slideIndex: this.state.focusLandfill,
      afterSlide: this.afterSlide
    };
    return (
        <div className={'section ' + classes.content}>
          <EChart ref='echart' onInit={this.init}
                  style={mapStyle}  option={this.state.option}
                  theme="dark" className={classes.map} />
          <Carousel style={cardStyle} {...sliderSettings}>
            {landfills.map(l =>
               <LandfillCard key={l.name} landfillInfo={l} />
            )}
          </Carousel>
        </div>
    );
  }
  init() {
    this.refs.echart.getInstance().on('click', this.onClickLandfill);
  }
  onClickLandfill(e) {
    const index = _.findIndex(landfills, {name: e.name});
    this.setState({
      focusLandfill: index
    });
  }
}

export default Page;



